// Koden for  etablere sphere basert p, inspirert av  kodeeksempel: //
// del4/ammoConstraints/sphere.js                                     //
////////////////////////////////////////////////////////////////////////

import * as THREE from "three";
import {addMeshToScene} from "./myThreeHelper.js";
import {createAmmoRigidBody, g_ammoPhysicsWorld, g_rigidBodies} from "./myAmmoHelper.js";
import {COLLISION_GROUP_SPHERE, 
	COLLISION_GROUP_COMPOUND, 
	COLLISION_GROUP_BOX, 
	COLLISION_GROUP_MOVEABLE, 
	COLLISION_GROUP_PLANE, 
	COLLISION_GROUP_CONVEX} from "./myAmmoHelper.js";

export function createSphere(radius = 10,mass = 10, color=0x00FF00, position={x:-30, y:70, z:30}, name = "sphere") {
	radius = radius * 0.2
	// THREE:
	let mesh = new THREE.Mesh(
		new THREE.SphereGeometry(radius, 32, 32),
		new THREE.MeshStandardMaterial({color: color}));
	mesh.name = name;
	mesh.position.set(position.x, position.y, position.z);
	mesh.castShadow = true;
	mesh.receiveShadow = true;
	mesh.collisionResponse = (mesh1) => {
		mesh1.material.color.setHex(Math.random() * 0xffffff);
	};

	// AMMO:
	let shape = new Ammo.btSphereShape(mesh.geometry.parameters.radius);
	shape.setMargin( 0.05 );
	let rigidBody = createAmmoRigidBody(shape, mesh, 0.7, 0, position, mass);

	mesh.userData.physicsBody = rigidBody;

	// Legger til PHYSICS WORLD:
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_SPHERE,
		COLLISION_GROUP_SPHERE | 
		COLLISION_GROUP_COMPOUND | 
		COLLISION_GROUP_CONVEX | 
		COLLISION_GROUP_BOX | 
		COLLISION_GROUP_MOVEABLE | 
		COLLISION_GROUP_PLANE);

	addMeshToScene(mesh);
	g_rigidBodies.push(mesh);
	rigidBody.threeMesh = mesh;
}
