import '../../style.css';
import * as THREE from "three";
import {
	addTwoLines,
	createThreeScene,
	g_scene,
	handleKeys,
	onWindowResize,
	renderScene,
	updateThree,
	loadModels,
	g_camera,
} from "./myThreeHelper.js";

import {
	createAmmoWorld,
	updatePhysics
} from "./myAmmoHelper.js";
import {createXZPlane} from "./xzplane.js";
import {createMovableGate} from "./movableGate.js";
import {createSphere} from "./sphere.js";
import {createFunnelTriangleMesh} from "./funnelTriangleMesh.js";
import {createHingedSeesaw} from "./hingedSeesaw.js";
import {createBricks} from "./bricks.js";
import {createCompoundStopperBar} from './stopperBar.js';
import {createSlidingTubeTriangleMesh} from "./slidingTubeMesh";
import {createCanonTriangleMesh} from "./canonTriangleMesh.js";
import {createHingedPicture} from "./hingedPicture.js";
import {createWall} from './wall';
import {createGrassPlane} from "./grassPlane";

let g_clock;
const g_currentlyPressedKeys = []
const XZPLANE_SIDELENGTH = 100;

Ammo().then( async function( AmmoLib ) {
	Ammo = AmmoLib;
	await main();
} );

export async function main() {

	createThreeScene();

	createAmmoWorld(true);

	await addAmmoSceneObjects();

	g_clock = new THREE.Clock();

	window.addEventListener('resize', onWindowResize, false);
	document.addEventListener('keyup', handleKeyUp, false);
	document.addEventListener('keydown', handleKeyDown, false);

	animate(0);
}

function handleKeyUp(event) {
	g_currentlyPressedKeys[event.code] = false;
}

function handleKeyDown(event) {
	g_currentlyPressedKeys[event.code] = true;
}

async function addAmmoSceneObjects() {
	// sound

	loadModels()

	let listener = new THREE.AudioListener();
	g_camera.add( listener );
	let sound = new THREE.Audio( listener );

	let purpleSphere = 0x8B008B;
	createXZPlane(XZPLANE_SIDELENGTH);
	createSphere(12, 70, 0x0000F2, {x:-16, y:99.0, z:17});
	createMovableGate(0x0000F2, {x:-16, y:92.7, z:17});
	createHingedSeesaw();
	createWall();
	createSphere(6, 25, 0x0000F2, {x:-15, y:7, z:44});
	await createFunnelTriangleMesh();
	await createSlidingTubeTriangleMesh({x: -27, y: 16, z: 0}, 0);
	createCompoundStopperBar();
	createBricks(4, 0xC28EE2, {x: 0, z: -15}, 30, sound);
	await createCanonTriangleMesh();
	await createHingedPicture(sound);
	addTwoLines("pictureAnchor", "pictureWindow");
	createSphere(12, 10, 0xFF0000, {x:-27, y:2, z:-10});
	createSphere(8, 8, 0xFF0000, {x:30, y:20, z:5.5}, "canonBall");
	createSphere(7.1, 8, purpleSphere, {x:-25, y:28, z:-12});

	await createGrassPlane(40, 20,  {x:2, y:0.5, z:-16});
}

function animate(currentTime, myThreeScene, myAmmoPhysicsWorld) {
	window.requestAnimationFrame((currentTime) => {
		animate(currentTime, myThreeScene, myAmmoPhysicsWorld);
	});
	let deltaTime = g_clock.getDelta();

	//Oppdaterer grafikken:
	updateThree(deltaTime);

	//Oppdaterer fysikken:
	updatePhysics(deltaTime);

	//Sjekker input:
	handleKeys(deltaTime, g_currentlyPressedKeys);

	//Tegner scenen med gitt kamera:
	renderScene();
}