//////////////////////////////////////////////////////////////////////////////////////////////////////////////
// Koden for  etablere movableGate basert p, insprirert av  kodeeksempel:                                 //
// del4/ammoConstraints/movable.js                                                                          //
//////////////////////////////////////////////////////////////////////////////////////////////////////////////

import * as THREE from "three";
import {addMeshToScene} from "./myThreeHelper.js";
import {
	createAmmoRigidBody,
	g_ammoPhysicsWorld,
	g_rigidBodies,
	COLLISION_GROUP_MOVEABLE,
	COLLISION_GROUP_SPHERE,
	COLLISION_GROUP_CONVEX
} from "./myAmmoHelper.js";

export function createMovableGate(color=0x00A6E5, position={x:-10, y:0, z:-30}) {
	const dimension1 = 5.5;
	const dimension2 = 0.5;
	const dimension3 = 5.9;
	const mass = 0;

	// THREE:
	let mesh = new THREE.Mesh(
		new THREE.BoxGeometry(dimension1, dimension2, dimension3, 1, 1),
		new THREE.MeshStandardMaterial({color: color}));
	mesh.name = 'movableGate';
	position.y = position.y + mesh.scale.y*dimension1/2;
	mesh.position.set(position.x, position.y, position.z);
	mesh.castShadow = true;
	mesh.receiveShadow = true;

	// AMMO:
	let width = mesh.geometry.parameters.width;
	let height = mesh.geometry.parameters.height;
	let depth = mesh.geometry.parameters.depth;


	let shape = new Ammo.btBoxShape( new Ammo.btVector3( width/2, height/2, depth/2) );
	let rigidBody = createAmmoRigidBody(shape, mesh, 0.7, 0.8, position, mass);
	// Flgende er avgjrende for  kunne flytte p objektet:
	// 2 = BODYFLAG_KINEMATIC_OBJECT: Betyr kinematic object, masse=0 men kan flyttes!
	rigidBody.setCollisionFlags(rigidBody.getCollisionFlags() | 2);
	// 4 = BODYSTATE_DISABLE_DEACTIVATION, dvs. "Never sleep".
	rigidBody.setActivationState(4);
	mesh.userData.physicsBody = rigidBody;

	// Legger til PHYSICS WORLD:
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_MOVEABLE,
			COLLISION_GROUP_SPHERE |
			COLLISION_GROUP_CONVEX
	);

	addMeshToScene(mesh);
	g_rigidBodies.push(mesh);
	rigidBody.threeMesh = mesh;
}
