import * as THREE from "three";
import {addMeshToScene} from "./myThreeHelper.js";
import {
	createAmmoRigidBody,
	g_ammoPhysicsWorld,
	g_rigidBodies,
	COLLISION_GROUP_BOX,
	COLLISION_GROUP_PLANE,
	COLLISION_GROUP_SPHERE,
	COLLISION_GROUP_MOVEABLE,
	COLLISION_GROUP_COMPOUND,
} from "./myAmmoHelper.js";

export async function createWall(mass = 0, position={x:3, y:5, z:-5}) {
    const width = 42;
    const height = 10;
    const depth = 1;

	//THREE
    const loader = new THREE.TextureLoader();
    // glass_window.jpg bilde er hentet fra: 
    // https://www.vecteezy.com/free-vector/seamless-brick-pattern
	const wallTexture = await loader.loadAsync('../../../assets/textures/brick-wall.jpg');

	let mesh = new THREE.Mesh(
		new THREE.BoxGeometry(width, height, depth, 1, 1),
		new THREE.MeshStandardMaterial({map: wallTexture}));
	mesh.name = 'box';
	mesh.position.set(position.x, position.y, position.z);
	mesh.castShadow = true;
	mesh.receiveShadow = true;

	//AMMO
	let shape = new Ammo.btBoxShape( new Ammo.btVector3( width/2, height/2, depth/2) );
	shape.setMargin( 0.05 );
	let rigidBody = createAmmoRigidBody(shape, mesh, 0.7, 0.8, position, mass);
	rigidBody.setActivationState(4);
	mesh.userData.physicsBody = rigidBody;

	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_BOX,
		COLLISION_GROUP_BOX | 
        COLLISION_GROUP_SPHERE | 
        COLLISION_GROUP_MOVEABLE | 
        COLLISION_GROUP_PLANE | 
        COLLISION_GROUP_COMPOUND
	);

	addMeshToScene(mesh);
	g_rigidBodies.push(mesh);
	rigidBody.threeMesh = mesh;
}