//// GrassPlane som lager en planeMesh med variert hyde, basert p //
//// implementasjon fra eksamen 2022.                                 //

import * as THREE from "three";
import {addMeshToScene} from "./myThreeHelper.js";

export async function createGrassPlane(x = 10, y = 10, position = {x: 0, y: 0, z: 0}) {
    let texture = await new THREE.TextureLoader().load("../../../assets/textures/green_grass.jpg");
    let geometry = new THREE.PlaneGeometry(x, y, 10, 10);
    let material = new THREE.MeshPhongMaterial({map: texture});
    material.side = THREE.DoubleSide;
    // geometry.rotateX(-Math.PI / 2);
    let planeMesh = new THREE.Mesh(geometry, material);
    planeMesh.name = "grassMesh";
    planeMesh.rotation.x = Math.PI / 2;
    planeMesh.position.set(position.x, position.y, position.z);

    setRandomHeights(geometry);

    addMeshToScene(planeMesh)
}

function setRandomHeights(geometry, scale= 1) {
    const vertexPositions = geometry.attributes.position.array;
    for (let i = 0; i < vertexPositions.length; i += 3) {
        vertexPositions[i + 2] = Math.random() * scale - scale / 2;
    }
    geometry.attributes.position.needsUpdate = true;
    geometry.computeVertexNormals();
}
