import * as THREE from "three";
import {createAmmoRigidBody, g_ammoPhysicsWorld, g_rigidBodies} from "./myAmmoHelper";
import {addArrowHelper, addMeshToScene} from "./myThreeHelper";
import {
	COLLISION_GROUP_BOX, 
	COLLISION_GROUP_COMPOUND,
	COLLISION_GROUP_CONVEX,
    COLLISION_GROUP_HINGE_CYLINDER,
	COLLISION_GROUP_MOVEABLE,
	COLLISION_GROUP_PLANE,
	COLLISION_GROUP_SPHERE
} from "./myAmmoHelper";

export function createHingedSeesaw(mass = 10, color=0x00FF00, position={x:0, y:0, z:0}) {

	const rigidBodyBeam = createCompoundBeam();
	const rigidBodyAnchor = createAnchor();
	const beamLength = rigidBodyBeam.threeMesh.children[0].geometry.parameters.width;
	const beamHalfHeight = rigidBodyBeam.threeMesh.children[0].geometry.parameters.height/2;
	const anchorRadius = rigidBodyAnchor.threeMesh.geometry.parameters.radiusTop;

	const anchorPivot = new Ammo.btVector3( 0, anchorRadius + beamHalfHeight, 0 );
	const anchorAxis = new Ammo.btVector3(1,0,0);
	const beamPivot = new Ammo.btVector3( 0, 0, 0 );
	const beamAxis = new Ammo.btVector3(1,0,0);
	const hingeConstraint = new Ammo.btHingeConstraint(
		rigidBodyAnchor,
		rigidBodyBeam,
		anchorPivot,
		beamPivot,
		anchorAxis,
		beamAxis,
		false
	);

	const lowerLimit = -Math.PI/12;
	const upperLimit = Math.PI/12;
	const softness = 0.9;
	const biasFactor = 0.1;
	const relaxationFactor = 0.1;
	hingeConstraint.setLimit( lowerLimit, upperLimit, softness, biasFactor, relaxationFactor);
	hingeConstraint.enableAngularMotor(true, 0, 0.4);
	g_ammoPhysicsWorld.addConstraint( hingeConstraint, false );
	g_ammoPhysicsWorld.addConstraint( hingeConstraint, false );
}

export function createCompoundBeam() {
	const mass=2;
	let beamSize = {width: 30, height: 1, depth: 3};
	let beamPosition={x:0, y:0, z:0};
	let pocketBlockSize = {width:3.5, height:1.5, depth:0.5};
	let pocketBlockPosition ={x:0, y:beamSize.height/2 + pocketBlockSize.height/2, z:beamSize.width/2 - pocketBlockSize.depth/2};
	let compoundBeamPosition = {x:-15, y:2.5, z:32};
	let compoundMesh = new THREE.Group();
	compoundMesh.userData.tag = 'compoundBeam';

	// THREE:
    // Beam
	let geometryBeam = new THREE.BoxGeometry(beamSize.width, beamSize.height, beamSize.depth, 1, 1);
	let meshBeam = new THREE.Mesh(geometryBeam, new THREE.MeshPhongMaterial({color: 0xf78a1d}));
	geometryBeam.rotateY(Math.PI/2);
	meshBeam.position.set(beamPosition.x, beamPosition.y, beamPosition.z);
	meshBeam.castShadow = true;
	compoundMesh.add(meshBeam);
	// Sphere pocket block 1
	let geometryBlock1 = new THREE.BoxGeometry(pocketBlockSize.width, pocketBlockSize.height, pocketBlockSize.depth, 1, 1);
	let meshBlock1 = new THREE.Mesh(geometryBlock1, new THREE.MeshPhongMaterial({color: 0x9DB031}));
	meshBlock1.position.set(pocketBlockPosition.x, pocketBlockPosition.y, pocketBlockPosition.z);
	meshBlock1.castShadow = true;
	compoundMesh.add(meshBlock1);
	// Sphere pocket block 2
	let geometryBlock2 = new THREE.BoxGeometry(pocketBlockSize.width, pocketBlockSize.height, pocketBlockSize.depth, 1, 1);
	let meshBlock2 = new THREE.Mesh(geometryBlock2, new THREE.MeshPhongMaterial({color: 0x9DB031}));
	geometryBlock2.rotateY(Math.PI/2);
	meshBlock2.position.set(
		pocketBlockPosition.x - pocketBlockSize.width/2 + pocketBlockSize.depth/2, 
		pocketBlockPosition.y, 
		pocketBlockPosition.z - pocketBlockSize.width/2 + pocketBlockSize.depth/2);
	meshBlock2.castShadow = true;
	compoundMesh.add(meshBlock2);
	// Sphere pocket block 3
	let geometryBlock3 = new THREE.BoxGeometry(pocketBlockSize.width, pocketBlockSize.height, pocketBlockSize.depth, 1, 1);
	let meshBlock3 = new THREE.Mesh(geometryBlock3, new THREE.MeshPhongMaterial({color: 0x9DB031}));
	geometryBlock3.rotateY(Math.PI/2);
	meshBlock3.position.set(
		pocketBlockPosition.x + pocketBlockSize.width/2 - pocketBlockSize.depth/2, 
		pocketBlockPosition.y, 
		pocketBlockPosition.z - pocketBlockSize.width/2 + pocketBlockSize.depth/2);
	meshBlock3.castShadow = true;
	compoundMesh.add(meshBlock3);
	// Sphere pocket block 4
	let geometryBlock4 = new THREE.BoxGeometry(pocketBlockSize.width, pocketBlockSize.height, pocketBlockSize.depth, 1, 1);
	let meshBlock4 = new THREE.Mesh(geometryBlock4, new THREE.MeshPhongMaterial({color: 0x9DB031}));
	meshBlock4.position.set(
		pocketBlockPosition.x, 
		pocketBlockPosition.y, 
		pocketBlockPosition.z - pocketBlockSize.width + pocketBlockSize.depth);
	meshBlock4.castShadow = true;
	compoundMesh.add(meshBlock4);

	compoundMesh.castShadow = true;
	compoundMesh.receiveShadow = true;

	// AMMO:
	let compoundShape = new Ammo.btCompoundShape();
	let beamShape = new Ammo.btBoxShape(new Ammo.btVector3(beamSize.width/2, beamSize.height/2, beamSize.depth/2));
	let pocketBlockShape = new Ammo.btBoxShape(new Ammo.btVector3(
		pocketBlockSize.width/2, pocketBlockSize.height/2, pocketBlockSize.depth/2));

	// Beam
	let transform1 = new Ammo.btTransform();
	transform1.setIdentity();
	transform1.setOrigin(new Ammo.btVector3(meshBeam.position.x, meshBeam.position.y, meshBeam.position.z));
	let quaternion1 = new THREE.Quaternion();
	quaternion1.setFromAxisAngle(new THREE.Vector3( 0, 1, 0 ), Math.PI/2);
	transform1.setRotation(new Ammo.btQuaternion( quaternion1.x, quaternion1.y, quaternion1.z, quaternion1.w ));
	compoundShape.addChildShape(transform1, beamShape);
	// PocketBlock1
	let transform2 = new Ammo.btTransform();
	transform2.setIdentity();
	transform2.setOrigin(new Ammo.btVector3(meshBlock1.position.x, meshBlock1.position.y, meshBlock1.position.z));
	compoundShape.addChildShape(transform2, pocketBlockShape);
	// PocketBlock2
	let transform3 = new Ammo.btTransform();
	transform3.setIdentity();
	transform3.setOrigin(new Ammo.btVector3(meshBlock2.position.x, meshBlock2.position.y, meshBlock2.position.z));
	let quaternion3 = new THREE.Quaternion();
	quaternion3.setFromAxisAngle(new THREE.Vector3( 0, 1, 0 ), Math.PI/2);
	transform3.setRotation(new Ammo.btQuaternion( quaternion3.x, quaternion3.y, quaternion3.z, quaternion3.w ));
	compoundShape.addChildShape(transform3, pocketBlockShape);
	// PocketBlock3
	let transform4 = new Ammo.btTransform();
	transform4.setIdentity();
	transform4.setOrigin(new Ammo.btVector3(meshBlock3.position.x, meshBlock3.position.y, meshBlock3.position.z));
	let quaternion4 = new THREE.Quaternion();
	quaternion4.setFromAxisAngle(new THREE.Vector3( 0, 1, 0 ), Math.PI/2);
	transform4.setRotation(new Ammo.btQuaternion( quaternion4.x, quaternion4.y, quaternion4.z, quaternion4.w ));
	compoundShape.addChildShape(transform4, pocketBlockShape);
	// PocketBlock4
	let transform5 = new Ammo.btTransform();
	transform5.setIdentity();
	transform5.setOrigin(new Ammo.btVector3(meshBlock4.position.x, meshBlock4.position.y, meshBlock4.position.z));
	compoundShape.addChildShape(transform5, pocketBlockShape);

	compoundShape.setMargin( 0.05 );

	let rigidBody = createAmmoRigidBody(compoundShape, compoundMesh, 0.3, 0.0, compoundBeamPosition, mass);
	rigidBody.setDamping(0.1, 0.5);
	rigidBody.setActivationState(4);
	compoundMesh.userData.physicsBody = rigidBody;

	// Legger til PHYSICS WORLD:
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_COMPOUND,
			COLLISION_GROUP_SPHERE |
			COLLISION_GROUP_COMPOUND |
			COLLISION_GROUP_HINGE_CYLINDER |
			COLLISION_GROUP_CONVEX |
			COLLISION_GROUP_BOX |
			COLLISION_GROUP_MOVEABLE |
			COLLISION_GROUP_PLANE );

	addMeshToScene(compoundMesh);
	g_rigidBodies.push(compoundMesh);
	rigidBody.threeMesh = compoundMesh;
	return rigidBody;
}

export function createAnchor(color=0xb846db) {
	let anchorSize ={radiusTop:2, radiusBottom:2, height:5};
	let anchorPosition={x:-15, y:anchorSize.radiusTop, z:30};
	const mass = 0;

	// THREE:
	let geometryAnchor = new THREE.CylinderGeometry(anchorSize.radiusTop, anchorSize.radiusBottom, anchorSize.height);
	geometryAnchor.rotateZ(-Math.PI/2);
	const mesh = new THREE.Mesh(geometryAnchor, new THREE.MeshStandardMaterial({color: color}));
	mesh.name = 'seesawAnchor';
	mesh.position.set(anchorPosition.x, anchorPosition.y, anchorPosition.z);
	mesh.castShadow = true;
	mesh.receiveShadow = true;
	mesh.collisionResponse = (mesh1) => {
		mesh1.material.color.setHex(Math.random() * 0xffffff);
		
	};
	// AMMO:
	const anchorShape = new Ammo.btCylinderShape(new Ammo.btVector3(anchorSize.radiusTop, anchorSize.radiusBottom, anchorSize.height/2));
	anchorShape.setMargin( 0.05 );
	const rigidBody = createAmmoRigidBody(anchorShape, mesh, 0.4, 0.6, anchorPosition, mass);
	mesh.userData.physicsBody = rigidBody;
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_HINGE_CYLINDER,
		COLLISION_GROUP_SPHERE | 
		COLLISION_GROUP_BOX | 
		COLLISION_GROUP_COMPOUND| 
		COLLISION_GROUP_MOVEABLE | 
		COLLISION_GROUP_CONVEX | 
		COLLISION_GROUP_PLANE );
	g_rigidBodies.push(mesh);
	rigidBody.threeMesh = mesh;

	addMeshToScene(mesh);
	g_rigidBodies.push(mesh);
	rigidBody.threeMesh = mesh;

	return rigidBody;
}