// Koden for  etablere hingedPicture basert p, insprirert av  kodeeksempel:                               //
// del4/ammoConstraints/hingedarm.js og pendulum.js (eksamen22)                                             //
// Bruk av colission.Response er basert p koden fra sphere.js                                              //
// Bruk av TextureLoader er basert p koden fra del4/ammoAdvancedShapes2trianglemesh/helicopterTriangleMesh //
// Bruk av AudioLoader for a lage lyd er hentet fra https://threejs.org/docs/#api/en/audio/Audio            //
//////////////////////////////////////////////////////////////////////////////////////////////////////////////

import * as THREE from "three";
import {createAmmoRigidBody, g_ammoPhysicsWorld, g_rigidBodies} from "./myAmmoHelper";
import {addMeshToScene} from "./myThreeHelper";
import {createParticles} from "./particles";

import {
	COLLISION_GROUP_SPHERE_ANCHOR,
	COLLISION_GROUP_BOX,
	COLLISION_GROUP_PLANE,
	COLLISION_GROUP_SPHERE
} from "./myAmmoHelper";

const CC_DISTANCE = 50;

export async function createHingedPicture(sound) {
	const rigidBodyAnchor = await createHingedPictureAnchor();
	const rigidBodyArm = await createHingedPictureFrame(sound);
	addHingedPictureConstraint(rigidBodyAnchor, rigidBodyArm, CC_DISTANCE);
}

function addHingedPictureConstraint(rigidBody1, rigidBody2, cc_dist) {
	const anchorPivot = new Ammo.btVector3( 0, 0, 0 );
	const anchorAxis = new Ammo.btVector3(1,0,0);
	const armPivot = new Ammo.btVector3( 0, cc_dist, 0 );
	const armAxis = new Ammo.btVector3(1,0,0);
	const hingeConstraint = new Ammo.btHingeConstraint(
		rigidBody1,
		rigidBody2,
		anchorPivot,
		armPivot,
		anchorAxis,
		armAxis,
		false
	);

	const lowerLimit = -Math.PI/6;
	const upperLimit = Math.PI/6;
	const softness = 0.8;
	const biasFactor = 0.25;
	const relaxationFactor = 1.0;
	hingeConstraint.setLimit( lowerLimit, upperLimit, softness, biasFactor, relaxationFactor);
	hingeConstraint.enableAngularMotor(true, 0, 0.5);
	g_ammoPhysicsWorld.addConstraint( hingeConstraint, false );
}

async function createHingedPictureAnchor() {
    let position = {x:30, y:80, z:20};
    const radius = 2;
	const wSeg = 25;
	const hSeg = 25;
	const massPictureAnchor = 0;
	const meshPictureAnchor = new THREE.Mesh(
		new THREE.SphereGeometry(radius, wSeg, hSeg),
		new THREE.MeshStandardMaterial({color: 0xfd9c68}));
	meshPictureAnchor.name = 'pictureAnchor';
	meshPictureAnchor.position.set(position.x, position.y, position.z);
	meshPictureAnchor.castShadow = true;
	meshPictureAnchor.receiveShadow = true;

	//AMMO
	const shapePictureAnchor = new Ammo.btSphereShape(meshPictureAnchor.geometry.parameters.radius);
	shapePictureAnchor.setMargin( 0.05 );
	const rigidBody = createAmmoRigidBody(shapePictureAnchor, meshPictureAnchor, 0.4, 0.6, position, massPictureAnchor);
	meshPictureAnchor.userData.physicsBody = rigidBody;
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_SPHERE_ANCHOR,
		    COLLISION_GROUP_SPHERE | 
            COLLISION_GROUP_BOX | 
            COLLISION_GROUP_PLANE );
	g_rigidBodies.push(meshPictureAnchor);
	rigidBody.threeMesh = meshPictureAnchor;

	addMeshToScene(meshPictureAnchor);
	g_rigidBodies.push(meshPictureAnchor);
	rigidBody.threeMesh = meshPictureAnchor;

	return rigidBody;
}

async function createHingedPictureFrame(sound) {
	const massPictureFrame=5;
	const width = 20;
	const height = 20;
	const depth = 0.5;
	let position = {x:30, y:30, z:20};

	//THREE
	let audioLoader = new THREE.AudioLoader();
	const loader = new THREE.TextureLoader();
    // glass_window.jpg bilde er hentet fra: 
    // https://depositphotos.com/42046701/stock-photo-smooth-gradient-background-sheet-of.html
	const pictureTexture = await loader.loadAsync('../../../assets/textures/glass_window.jpg');

	const meshPictureFrame = new THREE.Mesh(
		new THREE.BoxGeometry(width, height, depth),
		new THREE.MeshPhongMaterial({map: pictureTexture }));
	meshPictureFrame.name = 'pictureWindow';
	meshPictureFrame.position.set(position.x, position.y, position.z);
	meshPictureFrame.castShadow = true;
	meshPictureFrame.receiveShadow = true;
	let hasNotPlayed = true;
	meshPictureFrame.collisionResponse = (mesh1) => {
		if (hasNotPlayed) {
			updateTexture(mesh1);
			playSound(audioLoader, sound);
			createParticles();
		}
		hasNotPlayed = false;
	};

	//AMMO
	const shapePictureFrame = new Ammo.btBoxShape( new Ammo.btVector3( width/2, height/2, depth/2) );
	shapePictureFrame.setMargin( 0.05 );
	const rigidBody = createAmmoRigidBody(shapePictureFrame, meshPictureFrame, 0.7, 0.8, position, massPictureFrame);
	meshPictureFrame.userData.physicsBody = rigidBody;
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_BOX,
		    COLLISION_GROUP_SPHERE_ANCHOR |
            COLLISION_GROUP_SPHERE | 
            COLLISION_GROUP_BOX | 
            COLLISION_GROUP_PLANE );
	g_rigidBodies.push(meshPictureFrame);
	rigidBody.threeMesh = meshPictureFrame;

	addMeshToScene(meshPictureFrame);
	g_rigidBodies.push(meshPictureFrame);
	rigidBody.threeMesh = meshPictureFrame;
	return rigidBody;
}

async function updateTexture(mesh){
	// bruk av AudioLoader er hentet fra https://threejs.org/docs/#api/en/audio/Audio
	const loader = new THREE.TextureLoader();
	// glass_window.jpg bilde er hentet fra: 
    // https://depositphotos.com/42046701/stock-photo-smooth-gradient-background-sheet-of.html
	const pictureFrameTexture = await loader.loadAsync('../../../assets/textures/glass_window_broken.jpg');
	mesh.material.map = pictureFrameTexture;
	mesh.material.needsUpdate = true;
}

async function playSound(audioLoader, sound) {
	// Big-Glass-Breaking.mp3 fil er hentet fra 
	// https://www.fesliyanstudios.com/royalty-free-sound-effects-download/glass-shattering-and-breaking-124
	await audioLoader.load('../../../assets/sounds/Big-Glass-Breaking.mp3', function(buffer) {
		sound.setBuffer(buffer);
		sound.setLoop(false);
		sound.setVolume(0.2);
		sound.play()
	});
}