import * as THREE from "three";
import GUI from "lil-gui";
import {
	applyImpulse,
	moveRigidBody
} from "./myAmmoFunctionsHelper.js";
import {OrbitControls} from "three/examples/jsm/controls/OrbitControls.js";
import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader.js";
import * as SkeletonUtils from "three/examples/jsm/utils/SkeletonUtils.js";

export let g_scene;
export let g_camera;
let g_renderer, g_controls, g_lilGui;
let count = 0;

let g_models = {};

export function createThreeScene() {

	const canvas = document.createElement('canvas');
	document.body.appendChild(canvas);	
	
	g_renderer = new THREE.WebGLRenderer({canvas: canvas, antialias: true});
	g_renderer.setSize(window.innerWidth, window.innerHeight);
	g_renderer.setClearColor(0xBFD104, 0xff);  
	g_renderer.shadowMap.enabled = true; 
	g_renderer.shadowMapSoft = true;
	g_renderer.shadowMap.type = THREE.PCFSoftShadowMap; 
	
	g_scene = new THREE.Scene();
	g_scene.background = new THREE.Color( 0xd6f0f0 );
	
	g_lilGui = new GUI();
	
	addSkybox();
	addLights();
	
	g_camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 10000);
	g_camera.position.x = 30;
	g_camera.position.y = 160;
	g_camera.position.z = 100;
	
	g_controls = new OrbitControls(g_camera, g_renderer.domElement);
	g_controls.target.set(0, 0, 0);
	g_controls.addEventListener( 'change', renderScene);
}

// se reamde.txt fil for mer info om skybox (static/assets/readme.txt)
export function addSkybox() {
	let imagePrefix = "../../../assets/skyboxes/skybox1/";	
	let directions = ["px", "nx", "py", "ny", "pz", "nz"];
	let imageSuffix = ".jpg"; 
	let materialArray = [];
	for (let i = 0; i < 6; i++)
		materialArray.push(new THREE.MeshBasicMaterial({
			map:  new THREE.TextureLoader().load(imagePrefix + directions[i] + imageSuffix),
			side: THREE.BackSide
		}));

	let cubeWidth = 100;
	let cubeHeight = 120;
	let cubeDepth = 100;
	let boxGeometry = new THREE.BoxGeometry(cubeWidth, cubeHeight, cubeDepth);
	const boxMesh = new THREE.Mesh(boxGeometry, materialArray);
	boxMesh.name = "skyBoxMesh";
	boxMesh.position.x = 0;
	boxMesh.position.y = 59.9;
	boxMesh.position.z = 0;
	g_scene.add(boxMesh);
}

export function addLights() {
	// Ambient Light:
	let ambientLight = new THREE.AmbientLight(0xffffff, 0.7);
	ambientLight.visible = true;
	g_scene.add(ambientLight);
	
	const ambientFolder = g_lilGui.addFolder( 'Ambient Light' );
	ambientFolder.add(ambientLight, 'visible').name("On/Off");
	ambientFolder.add(ambientLight, 'intensity').min(0).max(1).step(0.01).name("Intensity");
	ambientFolder.addColor(ambientLight, 'color').name("Color");

	// Directional Light:
	let directionalLight = new THREE.DirectionalLight(0xffffff, 0.5);
	directionalLight.visible = true;
	directionalLight.position.set(0, 105, 0);
	directionalLight.castShadow = true;      
	directionalLight.shadow.camera.near = 0;
	directionalLight.shadow.camera.far = 301;
	directionalLight.shadow.camera.left = -50;
	directionalLight.shadow.camera.right = 50;
	directionalLight.shadow.camera.top = 250;
	directionalLight.shadow.camera.bottom = -250;
	directionalLight.shadow.camera.visible = true;
	g_scene.add(directionalLight);

	const directionalFolder = g_lilGui.addFolder( 'Directional Light' );
	directionalFolder.add(directionalLight, 'visible').name("On/Off");
	directionalFolder.add(directionalLight, 'intensity').min(0).max(1).step(0.01).name("Intensity");
	directionalFolder.addColor(directionalLight, 'color').name("Color");
}
 
export function handleKeys(delta, g_currentlyPressedKeys) {
	const canonBallMesh = g_scene.getObjectByName("canonBall");
	const lastBrick = g_scene.getObjectByName("brick0");

	if (g_currentlyPressedKeys['KeyI'] || (lastBrick.position.y > 1.38 && lastBrick.position.y < 1.39)) {
		if (count === 0){
			applyImpulse(canonBallMesh,{x:0, y:400, z:3});}
			count++;
	}

	const movableGate = g_scene.getObjectByName("movableGate");
	if (movableGate.position.x < -9 && g_currentlyPressedKeys['KeyD']) {	//D
		moveRigidBody(movableGate,{x: 0.2, y: 0, z: 0});
	}
}

export function onWindowResize() {
	g_camera.aspect = window.innerWidth / window.innerHeight;
	g_camera.updateProjectionMatrix();
	g_renderer.setSize(window.innerWidth, window.innerHeight);
	g_controls.handleResize();
	renderScene();
}

/////////////////////////////////////////////////////////////////////////////////////
// Koden ifm lines hentet fra kodeeksempel del3/lineconnection2/lineconnection2.js //
/////////////////////////////////////////////////////////////////////////////////////
export function updateThree(deltaTime) {	
	let line1MeshPosition1 = g_scene.getObjectByName("pictureAnchor", true);
	let line1MeshPosition2 = g_scene.getObjectByName("pictureWindow", true);
	let line2MeshPosition1 = g_scene.getObjectByName("pictureAnchor", true);
	let line2MeshPosition2 = g_scene.getObjectByName("pictureWindow", true);
	let line1Mesh = g_scene.getObjectByName("pictureFrameLineMesh1", true);
	let line2Mesh = g_scene.getObjectByName("pictureFrameLineMesh2", true);
	const line1VertexPositions = line1Mesh.geometry.attributes.position.array;
	const line2VertexPositions = line2Mesh.geometry.attributes.position.array;

	const frameWidth = line1MeshPosition2.geometry.parameters.width;

	const line1Pos1 = new THREE.Vector3();
	const line2Pos1 = new THREE.Vector3();
	line1MeshPosition1.getWorldPosition(line1Pos1);
	line2MeshPosition1.getWorldPosition(line2Pos1);
	line1VertexPositions[0] = line1Pos1.x;
	line1VertexPositions[1] = line1Pos1.y;
	line1VertexPositions[2] = line1Pos1.z;
	line2VertexPositions[0] = line2Pos1.x;
	line2VertexPositions[1] = line2Pos1.y;
	line2VertexPositions[2] = line2Pos1.z;

	const line1Pos2 = new THREE.Vector3();
	const line2Pos2 = new THREE.Vector3();
	line1MeshPosition2.getWorldPosition(line1Pos2);
	line2MeshPosition2.getWorldPosition(line2Pos2);
	line1VertexPositions[3] = line1Pos2.x - frameWidth/2;
	line1VertexPositions[4] = line1Pos2.y;
	line1VertexPositions[5] = line1Pos2.z;
	line2VertexPositions[3] = line2Pos2.x + frameWidth/2;
	line2VertexPositions[4] = line2Pos2.y;
	line2VertexPositions[5] = line2Pos2.z;
	line1Mesh.geometry.attributes.position.needsUpdate = true;
	line1Mesh.geometry.computeBoundingBox();
	line1Mesh.geometry.computeBoundingSphere();
	line2Mesh.geometry.attributes.position.needsUpdate = true;
	line2Mesh.geometry.computeBoundingBox();
	line2Mesh.geometry.computeBoundingSphere();

	g_controls.update();
}

export function addMeshToScene(mesh) {
	g_scene.add(mesh);
}

export function renderScene()
{
	g_renderer.render(g_scene, g_camera);
}

export function getRigidBodyFromMesh(meshName) {
	const mesh = g_scene.getObjectByName(meshName);
	if (mesh)
		return mesh.userData.physicsBody;
	else
		return null;
}

export function addArrowHelper(mesh, direction, origin, name, color=0xff0000, length=10) {
	const meshDirectionArrow = new THREE.ArrowHelper( direction, origin, length, color );
	meshDirectionArrow.name = name;
	mesh.add(meshDirectionArrow);
}

/////////////////////////////////////////////////////////////////////////////////////
// Koden ifm lines hentet fra kodeeksempel del3/lineconnection2/lineconnection2.js //
/////////////////////////////////////////////////////////////////////////////////////
export function addTwoLines(nameMesh1, nameMesh2) {
	let line1MeshPosition1 = g_scene.getObjectByName(nameMesh1, true);
	let line1MeshPosition2 = g_scene.getObjectByName(nameMesh2, true);
	let line2MeshPosition1 = g_scene.getObjectByName(nameMesh1, true);
	let line2MeshPosition2 = g_scene.getObjectByName(nameMesh2, true);
	const lineMaterial = new THREE.LineBasicMaterial( { color: 0x721dfc } );
	const points1 = [];
	const points2 = [];
	const startPoint1 = new THREE.Vector3();
	const endPoint1 = new THREE.Vector3();
	const startPoint2 = new THREE.Vector3();
	const endPoint2 = new THREE.Vector3();
	line1MeshPosition1.getWorldPosition(startPoint1);
	line1MeshPosition2.getWorldPosition(endPoint1);
	line2MeshPosition1.getWorldPosition(startPoint2);
	line2MeshPosition2.getWorldPosition(endPoint2);
	points1.push(startPoint1);
	points1.push(endPoint1);
	points2.push(startPoint2);
	points2.push(endPoint2);
	const lineGeometry1 = new THREE.BufferGeometry().setFromPoints( points1 );
	const lineGeometry2 = new THREE.BufferGeometry().setFromPoints( points2 );
	const pictureFrameLineMesh1 = new THREE.Line( lineGeometry1, lineMaterial );
	const pictureFrameLineMesh2 = new THREE.Line( lineGeometry2, lineMaterial );
	pictureFrameLineMesh1.name = "pictureFrameLineMesh1";
	pictureFrameLineMesh2.name = "pictureFrameLineMesh2";
	addMeshToScene(pictureFrameLineMesh1);
	addMeshToScene(pictureFrameLineMesh2);
}


/////////////////////////////////////////////////////////////////////////////////////
// Koden fra og med loadModels hentet fra kodeeksempel del6/loadgltfanimatedmodel1//
/////////////////////////////////////////////////////////////////////////////////////
export function loadModels() {
	const manager = new THREE.LoadingManager();
	manager.onLoad = () => {
		initModels();
	}

	g_models = {
		pc_nightmare_mushroom: {url: '../../../assets/models/pc_nightmare_mushroom/scene.gltf', scale: {x:0.2,y:0.2,z:0.2}, position: {x:0, y:5, z:-20}},
		pc_nightmare_mushroom1: {url: '../../../assets/models/pc_nightmare_mushroom/scene.gltf', scale: {x:0.2,y:0.2,z:0.2}, position: {x:-7, y:5, z:-17}},
		pc_nightmare_mushroom2: {url: '../../../assets/models/pc_nightmare_mushroom/scene.gltf', scale: {x:0.2,y:0.2,z:0.2}, position: {x:7, y:5, z:-22}},
		pc_nightmare_mushroom3: {url: '../../../assets/models/pc_nightmare_mushroom/scene.gltf', scale: {x:0.2,y:0.2,z:0.2}, position: {x:0, y:5, z:-10}},
	};

	const gltfLoader = new GLTFLoader(manager);
	for (const model of Object.values(g_models)) {
		gltfLoader.load(model.url, (gltf) => {
			model.gltf = gltf;
		});
	}

}

function initModels() {

	Object.values(g_models).forEach((model, index) => {
		const clonedScene = SkeletonUtils.clone(model.gltf.scene);
		const root = new THREE.Object3D();
		root.add(clonedScene);

		root.scale.set(model.scale.x, model.scale.y, model.scale.z);
		root.position.set(model.position.x, model.position.y, model.position.z);
		addMeshToScene(root);
	});
}
