import * as THREE from "three";
import {
	createAmmoRigidBody,
	g_rigidBodies,
	g_ammoPhysicsWorld,
	COLLISION_GROUP_CONVEX,
	COLLISION_GROUP_SPHERE,
	COLLISION_GROUP_BOX,
	COLLISION_GROUP_MOVEABLE,
	COLLISION_GROUP_PLANE,
    COLLISION_GROUP_COMPOUND,
} from "./myAmmoHelper.js";
import {addMeshToScene} from "./myThreeHelper.js";
import {createTriangleShapeAddToCompound} from "./triangleMeshHelpers.js";

export async function createCanonTriangleMesh(mass = 0, color = 0xf78a1d, position={x:30, y:0.5, z:-10}) {
	//Ammo-container:
	let compoundShape = new Ammo.btCompoundShape();
	//Three-container:
	let groupMesh = new THREE.Group();
	groupMesh.userData.tag = 'canon';
	groupMesh.position.x = 0;
	groupMesh.position.y = 0;
	groupMesh.position.z = 0;

    await createCanonParts(groupMesh, compoundShape);

	// Sett samme transformasjon p compoundShape som p bottomMesh:
	let rigidBody = createAmmoRigidBody(compoundShape, groupMesh, 0.4, 0.3, position, mass);
	groupMesh.userData.physicsBody = rigidBody;
	// Legger til physics world:
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_COMPOUND,
		COLLISION_GROUP_CONVEX | COLLISION_GROUP_SPHERE | COLLISION_GROUP_BOX | COLLISION_GROUP_MOVEABLE | COLLISION_GROUP_PLANE );

	addMeshToScene(groupMesh);
	g_rigidBodies.push(groupMesh);
	rigidBody.threeMesh = groupMesh;
}

async function createCanonParts(groupMesh, compoundShape){
	//Canon Base:
    const width = 15;
    const height = 1;
    const depth = 7;
	let canonBaseGeometry = new THREE.BoxGeometry(width, height, depth);
	let canonBaseMaterial = new THREE.MeshPhongMaterial({ color:0x00de88});
	let canonBaseMesh = new THREE.Mesh(canonBaseGeometry, canonBaseMaterial);
	canonBaseMesh.name = "canonBase";
    canonBaseMesh.rotation.y = Math.PI / 2;
	canonBaseMesh.position.x = 0;
	canonBaseMesh.position.y = 0;
	canonBaseMesh.position.z = 0;
	groupMesh.add(canonBaseMesh);
	createTriangleShapeAddToCompound(compoundShape, canonBaseMesh);

    //Informasjon ifm etablering av Tube Three.js er hentet fra: https://threejs.org/docs/#api/en/geometries/TubeGeometry
    //Canon Tube:
    class CannonTubeCurve extends THREE.Curve {
        constructor( scale = 1 ) {
            super();
            this.scale = scale;
        }
        getPoint( t, optionalTarget = new THREE.Vector3() ) {
            const tx = 1;
            const ty = t*20;
            const tz = 0;
            return optionalTarget.set( tx, ty, tz ).multiplyScalar( this.scale );
        }
    }

    let tubePath = new CannonTubeCurve(1);
	let canonTubeGeometry = new THREE.TubeGeometry( tubePath, 20, 2.5, 8, false );
	let canonTubeMaterial = new THREE.MeshPhongMaterial({color:0x00de88, side: THREE.DoubleSide, transparent: true, opacity: 0.6});
	let canonTubeMesh = new THREE.Mesh(canonTubeGeometry, canonTubeMaterial);
	canonTubeMesh.name = "canonTube";
    canonTubeMesh.rotation.x = Math.PI / 5;
	canonTubeMesh.position.x = -1;
	canonTubeMesh.position.y = 0;
	canonTubeMesh.position.z = 3;
	groupMesh.add(canonTubeMesh);
	createTriangleShapeAddToCompound(compoundShape, canonTubeMesh);
}