// Koden for  lage brikker basert p, insprirert av  kodeeksempel del4/ammoForces/box.js        //
// Bruk av AudioLoader for a lage lyd er hentet fra https://threejs.org/docs/#api/en/audio/Audio //
///////////////////////////////////////////////////////////////////////////////////////////////////

import * as THREE from "three";
import {addMeshToScene} from "./myThreeHelper.js";
import {
	createAmmoRigidBody,
	g_ammoPhysicsWorld,
	g_rigidBodies,
	COLLISION_GROUP_BOX,
	COLLISION_GROUP_PLANE,
	COLLISION_GROUP_SPHERE,
	COLLISION_GROUP_COMPOUND,
	COLLISION_GROUP_CONVEX
} from "./myAmmoHelper.js";

export function createBricks(mass = 4, color=0xC28EE2, center={x:0, z:-15}, circlePathRad = 30, sound) {
	const brickWidth = 2;
    const brickHeight = 5;
	const brickDepth = 0.5;
    let startAngle = Math.PI/2;
    const qtyOfBricks = 20;
    const angleOfRot = Math.PI/qtyOfBricks;
    let position={x:0, y:brickHeight/2, z:0};

    const audioLoader = new THREE.AudioLoader();

    for (let i = 0; i < qtyOfBricks; i ++)
    {

        // THREE:
        let brickMesh = new THREE.Mesh(
            new THREE.BoxGeometry(brickWidth, brickHeight, brickDepth, 1, 1),
            new THREE.MeshStandardMaterial({color: color}));
        brickMesh.rotateY(angleOfRot*(i+1));
        brickMesh.name = 'brick' + i.toString();
        position.x = center.x + (circlePathRad * Math.sin(startAngle + angleOfRot));
        position.z = center.z + (circlePathRad * Math.cos(startAngle + angleOfRot));
        startAngle += angleOfRot;
        brickMesh.position.set(position.x, position.y, position.z);

        brickMesh.castShadow = true;
        brickMesh.receiveShadow = true;

        let brickShape = new Ammo.btBoxShape( new Ammo.btVector3( brickWidth/2, brickHeight/2, brickDepth/2) );

        let transform = new Ammo.btTransform();
        transform.setIdentity();
        transform.setOrigin(new Ammo.btVector3(brickMesh.position.x, brickMesh.position.y, brickMesh.position.z));
        let quaternion = new THREE.Quaternion();
        quaternion.setFromAxisAngle(new THREE.Vector3( 0, 1, 0 ), angleOfRot*(i+1));
        transform.setRotation(new Ammo.btQuaternion( quaternion.x, quaternion.y, quaternion.z, quaternion.w ));

        brickShape.setMargin( 0.05 );
        let rigidBody = createAmmoRigidBody(brickShape, brickMesh, 0.5, 0.8, position, mass);
        rigidBody.setActivationState(4);

        brickMesh.userData.physicsBody = rigidBody;
        let hasRun = 0
        brickMesh.collisionResponse = () => {
            // mesh1.material.color.setHex(Math.random() * 0xffffff);
             if (hasRun === 0) {
                 playSound(audioLoader, sound).then(r => console.log(r));
             }
             hasRun = 1;
        };

        // Legger til PHYSICS WORLD:
        g_ammoPhysicsWorld.addRigidBody(
            rigidBody,
            COLLISION_GROUP_BOX,
            COLLISION_GROUP_BOX | COLLISION_GROUP_SPHERE | COLLISION_GROUP_PLANE | COLLISION_GROUP_COMPOUND | COLLISION_GROUP_CONVEX
        );

        addMeshToScene(brickMesh);
        g_rigidBodies.push(brickMesh);
        rigidBody.threeMesh = brickMesh;
    }
}


async function playSound(audioLoader, sound) {
    // lydfil https://pixabay.com/sound-effects/search/ogg/?theme=film%20%26%20special%20effects
    await audioLoader.load('../../../assets/sounds/splat.mp3', function(buffer) {
        sound.setBuffer(buffer);
        sound.setLoop(false);
        sound.setVolume(0.2);
        sound.play()
    });
}