import * as THREE from "three";
import {
	createAmmoRigidBody,
	g_rigidBodies,
	g_ammoPhysicsWorld,
	COLLISION_GROUP_SPHERE,
	COLLISION_GROUP_BOX,
	COLLISION_GROUP_MOVEABLE,
	COLLISION_GROUP_PLANE,
	COLLISION_GROUP_COMPOUND,
	COLLISION_GROUP_CONVEX
} from "./myAmmoHelper.js";
import {addMeshToScene} from "./myThreeHelper.js";

export function createCompoundStopperBar(mass = 10, position={x:-33, y:27.7, z:-9}) {
	// THREE:
    const lWidth = 14;
    const lHeight = 2.5;
    const lDepth = 2.5;
    const rWidth = 20;
    const rHeight = lHeight;
    const rDepth = lDepth;
    const sWidth = 10;
    const sHeight = 0.5;
    const sDepth = 20;

	// 1.Rod:
	let compoundMesh = new THREE.Group();
	compoundMesh.userData.tag = 'compoundBar';
	let geometryLeftBar = new THREE.BoxGeometry(lWidth, lHeight, lDepth);
	let meshLeftBar = new THREE.Mesh(geometryLeftBar, new THREE.MeshPhongMaterial({color: 0xf78a1d}));
    meshLeftBar.rotateY(-Math.PI/2);
	meshLeftBar.position.set(0, 0, 0);
	meshLeftBar.castShadow = true;
	compoundMesh.add(meshLeftBar);
	// 2.Left bar:
	let geometryRightBar = new THREE.BoxGeometry(rWidth, rHeight, rDepth);
	let meshRightBar = new THREE.Mesh(geometryRightBar, new THREE.MeshPhongMaterial({color: 0xf78a1d}));
    meshRightBar.rotateY(Math.PI/4);
	meshRightBar.position.set(10, 0, 0);
	meshRightBar.castShadow = true;
	compoundMesh.add(meshRightBar);
    // 3.Senter bar
	let geometryCenterBar = new THREE.BoxGeometry(sWidth, sHeight, sDepth);
	let meshCenterBar = new THREE.Mesh(geometryCenterBar, new THREE.MeshPhongMaterial({color: 0xf78a1d}));
    meshCenterBar.rotateY(Math.PI/10);
	meshCenterBar.position.set(20, 0, 11);
	meshCenterBar.castShadow = true;
	compoundMesh.add(meshCenterBar);

	// AMMO:
	let compoundShape = new Ammo.btCompoundShape();
	let leftBarShape = new Ammo.btBoxShape(new Ammo.btVector3(lWidth/2, lHeight/2, lDepth/2));
    let rightBarShape = new Ammo.btBoxShape(new Ammo.btVector3(rWidth/2, rHeight/2, rDepth/2));
    let centerBarShape = new Ammo.btBoxShape(new Ammo.btVector3(sWidth/2, sHeight/2, sDepth/2));

	// 1.LeftBar
	let transform1 = new Ammo.btTransform();
	transform1.setIdentity();
	transform1.setOrigin(new Ammo.btVector3(meshLeftBar.position.x, meshLeftBar.position.y, meshLeftBar.position.z));
    let quaternion1 = new THREE.Quaternion();
	quaternion1.setFromAxisAngle(new THREE.Vector3( 0, 1, 0 ), -Math.PI/2);
	transform1.setRotation(new Ammo.btQuaternion( quaternion1.x, quaternion1.y, quaternion1.z, quaternion1.w ));
	compoundShape.addChildShape(transform1, leftBarShape);
	// 2.RightBar
	let transform2 = new Ammo.btTransform();
	transform2.setIdentity();
	transform2.setOrigin(new Ammo.btVector3(meshRightBar.position.x, meshRightBar.position.y, meshRightBar.position.z));
    let quaternion2 = new THREE.Quaternion();
	quaternion2.setFromAxisAngle(new THREE.Vector3( 0, 1, 0 ), Math.PI/4);
	transform2.setRotation(new Ammo.btQuaternion( quaternion2.x, quaternion2.y, quaternion2.z, quaternion2.w ));
	compoundShape.addChildShape(transform2, rightBarShape);
    // 3.CenterBar
	let transform3 = new Ammo.btTransform();
	transform3.setIdentity();
	transform3.setOrigin(new Ammo.btVector3(meshCenterBar.position.x, meshCenterBar.position.y, meshCenterBar.position.z));
    let quaternion3 = new THREE.Quaternion();
	quaternion3.setFromAxisAngle(new THREE.Vector3( 0, 1, 0 ), Math.PI/10);
	transform3.setRotation(new Ammo.btQuaternion( quaternion3.x, quaternion3.y, quaternion3.z, quaternion3.w ));
	compoundShape.addChildShape(transform3, centerBarShape);

	let rigidBody = createAmmoRigidBody(compoundShape, compoundMesh, 0.2, 0.9, position, mass);
	compoundMesh.userData.physicsBody = rigidBody;
	// Legger til physics world:
	g_ammoPhysicsWorld.addRigidBody(
		rigidBody,
		COLLISION_GROUP_COMPOUND,
			COLLISION_GROUP_COMPOUND |
			COLLISION_GROUP_CONVEX |
			COLLISION_GROUP_SPHERE |
			COLLISION_GROUP_BOX |
			COLLISION_GROUP_MOVEABLE |
			COLLISION_GROUP_PLANE );

	addMeshToScene(compoundMesh);
	g_rigidBodies.push(compoundMesh);
	rigidBody.threeMesh = compoundMesh;
}