import * as THREE from "three";
import {addMeshToScene} from "./myThreeHelper.js";
import {
    COLLISION_GROUP_BOX,
    COLLISION_GROUP_COMPOUND,
    COLLISION_GROUP_CONVEX,
    COLLISION_GROUP_MOVEABLE,
    COLLISION_GROUP_PLANE,
    COLLISION_GROUP_SPHERE,
    createAmmoRigidBody,
    g_ammoPhysicsWorld,
    g_rigidBodies
} from "./myAmmoHelper.js";
import {createTriangleShapeAddToCompound} from "./triangleMeshHelpers";


export async function createSlidingTubeTriangleMesh(position, mass) {
    let compoundShape = new Ammo.btCompoundShape();
    let groupMesh = new THREE.Group();
    groupMesh.name = "slidingTubeTriangleMesh";
    groupMesh.position.copy(position);

    await createSlidingTubeParts(groupMesh, compoundShape);
    // Physics
    let rigidBody = createAmmoRigidBody(compoundShape, groupMesh, 0.4, 0.6, position, mass);
    groupMesh.userData.physicsBody = rigidBody;
    // Legger til physics world:
    g_ammoPhysicsWorld.addRigidBody(
        rigidBody,
        COLLISION_GROUP_COMPOUND,
        COLLISION_GROUP_CONVEX | COLLISION_GROUP_SPHERE | COLLISION_GROUP_COMPOUND| COLLISION_GROUP_MOVEABLE | COLLISION_GROUP_PLANE );

    addMeshToScene(groupMesh);
    g_rigidBodies.push(groupMesh);
    rigidBody.threeMesh = groupMesh;

}

async function createSlidingTubeParts(groupMesh, compoundShape) {

    // etablering av TubeMesh er fra https://threejs.org/docs/#api/en/geometries/TubeGeometry
    let textureLoader = new THREE.TextureLoader();
    let texture = await textureLoader.load("../../../assets/textures/green_grass.jpg");
    let path = new TubeCurve(7);
    let tubeGeometry = new THREE.TubeGeometry(path, 1000, 1.5, 20, false);
    let tubeMaterial = new THREE.MeshPhongMaterial({color: 0x00ff00});
    tubeMaterial.side = THREE.DoubleSide;
    tubeMaterial.opacity = 0.6;
    tubeMaterial.transparent = true;
    let tubeMesh = new THREE.Mesh(tubeGeometry, tubeMaterial);
    tubeMesh.name = "tubeMesh";
    tubeMesh.rotation.x = Math.PI / 2;
    tubeMesh.rotation.z = Math.PI / 3;
    tubeMesh.castShadow = true;
    groupMesh.add(tubeMesh);

    let tubePlatform = new THREE.Group();
    let redHex = 0xff0000;
    // Create tube platform
    let platformGeometry = new THREE.BoxGeometry(17, 0.3, 20);
    let platformMaterial = new THREE.MeshPhongMaterial({color: redHex});
    let platformMesh = new THREE.Mesh(platformGeometry, platformMaterial);
    platformMesh.position.set(-5, 9, -8);
    platformMesh.rotation.x = Math.PI / 10;
    platformMesh.rotation.y = Math.PI / 10;


    // setRandomHeights(platformGeometry)
    // apply height

    tubePlatform.add(platformMesh);
    // create barrier
    let barrierGeometry = new THREE.BoxGeometry(16, 4, 1);
    let barrierMaterial = new THREE.MeshPhongMaterial({color: redHex});
    let barrierMesh = new THREE.Mesh(barrierGeometry, barrierMaterial);
    barrierMesh.position.set(-0, 8.5, -5);
    barrierMesh.rotation.x = Math.PI / 10;
    barrierMesh.rotation.y = Math.PI / 2.5;

    tubePlatform.add(barrierMesh);

    let secondBarrierGeometry = new THREE.BoxGeometry(7, 4, 1);
    let secondBarrierMaterial = new THREE.MeshPhongMaterial({color: redHex});
    let secondBarrierMesh = new THREE.Mesh(secondBarrierGeometry, secondBarrierMaterial);
    secondBarrierMesh.position.set(-10, 8.5, 1);
    secondBarrierMesh.rotation.x = Math.PI / 10;
    secondBarrierMesh.rotation.y = Math.PI / 1.5;

    tubePlatform.add(secondBarrierMesh);

    groupMesh.add(tubePlatform);


    tubePlatform.position.z = 0;
    tubePlatform.position.y = 0.2;

    groupMesh.rotation.y = -0.5;

    //
    createTriangleShapeAddToCompound(compoundShape, barrierMesh);
    createTriangleShapeAddToCompound(compoundShape, platformMesh);
    createTriangleShapeAddToCompound(compoundShape, tubeMesh);
    createTriangleShapeAddToCompound(compoundShape, secondBarrierMesh);

}

// TubeCurve hentet fra https://threejs.org/docs/#api/en/geometries/TubeGeometry
class TubeCurve extends THREE.Curve {
   constructor( scale = 1 ) {
       super();

         this.scale = scale;
   }

   getPoint( t, optionalTarget = new THREE.Vector3() ) {
        let tx = Math.sin( 7.2* Math.PI * t );
        let ty = Math.cos( 7.2 * Math.PI * t );
        let tz = 3 * t - 1;

       return optionalTarget.set( tx, ty, tz ).multiplyScalar( this.scale );
   }
}

