import * as THREE from "three";
import {addMeshToScene} from "./myThreeHelper";
///////////////////////////////////////////////////////////////////////////////
// Koden for partikler er hentet i sin helhet fra kodeeksempel particles1.js //
///////////////////////////////////////////////////////////////////////////////
export async function createParticles() {
	//Materiale:
	let material = new THREE.PointsMaterial({
		size: 1,                //Paticle size
		vertexColors : true
	});

	// Liste med vertekser:
	let range = 2000;
	let vertices = [];
	let colors = [];
	let color = new THREE.Color();
	let x,y,z;
	for (let i = 0; i < 15000; i++) {
		x = THREE.MathUtils.randFloatSpread( range );
		y = THREE.MathUtils.randFloatSpread( range );
		z = THREE.MathUtils.randFloatSpread( range );
		vertices.push( x, y, z );
		color.setHex(Math.random() * 0xffffff);
		colors.push(color.r, color.g, color.b);
	}
	// Geometri:
	const geometry = new THREE.BufferGeometry();
	geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( vertices, 3 ) );
	geometry.setAttribute( 'color', new THREE.Float32BufferAttribute( colors, 3 ) );
	let points = new THREE.Points(geometry, material);
	addMeshToScene(points);
}